import { Link } from "react-router-dom";
import "./navbar.css";
import { useTranslation } from "react-i18next";

const Navbar = () => {
  const { t, i18n } = useTranslation();

  // const langs = [
  //   { code: "en", country_code: "gb" },
  //   { code: "it", country_code: "it" },
  // ];

  const languagesObj = {
    en: { name: "en" },
    it: { name: "it" },
  };

  let navLinks = [
    {
      name: t("home"),
      link: "/",
    },
    {
      name: "About",
      link: "/about",
    },
    {
      name: "Gallery",
      link: "/gallery",
    },
    {
      name: "Calender",
      link: "/calender",
    },
  ];
  return (
    <header className="nav__bar">
      <div className="logo unselectable">All That's Left To Me</div>
      <nav>
        <ul>
          <li>
            {Object.keys(languagesObj).map((lng) => (
              <button
                className="lang__btn"
                key={lng}
                style={{
                  cursor: "pointer",
                  background: "transparent",
                  outline: "none",
                  color: "white",
                }}
                type="submit"
                onClick={() => i18n.changeLanguage(lng)}
                disabled={i18n.resolvedLanguage === lng}
              >
                {languagesObj[lng].name}
              </button>
            ))}
          </li>

          {/* ////////////////// */}
          {navLinks.map((link, index) => (
            <li key={index}>
              <Link to={link.link}>{link.name}</Link>
            </li>
          ))}
        </ul>
      </nav>
    </header>
  );
};

export default Navbar;
